import React, { Component } from 'react';
import axios from 'axios';
import { JsonToTable } from "react-json-to-table";


export default class Form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      school: '',
      votes: 32,
    };
    this.state1 = {};


    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    const inputValue = event.target.value;
    const stateField = event.target.name;
    this.setState({
      [stateField]: inputValue,
    });
    console.log(this.state);
    console.log('this is:', this);
  }
  async handleSubmit(event) {
    event.preventDefault();
    const { school, votes } = this.state;
    //console.log(`"${school}"` , `${votes}`);
    console.log("Haciendo request");
    await axios.get('https://ldmjcwd3rk.execute-api.us-east-1.amazonaws.com/prod/voter/voter_01')
  .then(function (response) {
    // handle success
    
    window.value = response;
  })
  .catch(function (error) {
    // handle error
    console.log(error);
  })
  }

  handleClick = () => {  
    
    this.state1 = window.value.data
    /*var res = this.state.map(state1 => state1.pendingAmount).reduce((acc, state1) => state1 + acc);
    console.log(res)*/
  
  }

  

  render() {
    return (
      <div>
        <form onSubmit={this.handleSubmit}>
          <label>School:</label>
          <input
            type="text"
            name="school"
            onChange={this.handleChange}
            value={this.state.school}
          />

          <label>Votes:</label>
          <input
            type="number"
            name="votes"
            onChange={this.handleChange}
            value={this.state.votes}
          />

          <button type="submit">Send</button>
          
        </form>

        <button onClick={this.handleClick}>Get</button>

        <JsonToTable json={this.state1} />        
        
      </div>
    );
  }
}